import React from 'react';
import s from './App.module.css'
import Hero from "./components/Hero/Hero";
import Footer from "./components/SignInSide/Footer";
import Logo from "./components/SignInSide/Logo";
import Form from "./components/SignInSide/Form";

let widerScreenWidth = window.matchMedia("(max-width: 450px)");

const App = () => {
  return (
      <div className={s.mainContainer}>
        <section>
            {!widerScreenWidth.matches && <Hero/>}
        </section>
          <section className={s.signInContainer}>
                  <Logo/>
                  <h1 className={s.signInTitle}>Sign in</h1>
                  <div className={s.signUpText}>
                      <span>Don’t have an account?</span>
                      <a href="#">Sign up now</a>
                  </div>
                  <Form/>
                  <Footer/>
          </section>
      </div>
  );
};

export default App;
